import React from "react";
import {
  Drawer,
  Paper,
  Box,
  Tooltip,
  IconButton,
  Divider,
  Typography,
} from "@material-ui/core";
import GitHubIcon from "@material-ui/icons/GitHub";
import LinkedInIcon from "@material-ui/icons/LinkedIn";
import EmailIcon from "@material-ui/icons/Email";
import InfoOutlinedIcon from "@material-ui/icons/InfoOutlined";

function GitHub(props) {
  return (
    <Tooltip title="GitHub" placement={props.tooltip}>
      <IconButton
        size="large"
        href="https://github.com/wu000168"
        target="_blank"
        style={{ color: "#24292e" }}
      >
        <GitHubIcon />
      </IconButton>
    </Tooltip>
  );
}

function LinkedIn(props) {
  return (
    <Tooltip title="LinkedIn" placement={props.tooltip}>
      <IconButton
        size="large"
        href="https://www.linkedin.com/in/zhiyuan-wu/"
        target="_blank"
        style={{ color: "#0077b5" }}
      >
        <LinkedInIcon />
      </IconButton>
    </Tooltip>
  );
}

function Email(props) {
  return (
    <Tooltip title="Email" placement={props.tooltip}>
      <IconButton
        size="large"
        href="mailto://wu000168@umn.edu/"
        target="_blank"
        style={{ color: "#B23121" }}
      >
        <EmailIcon />
      </IconButton>
    </Tooltip>
  );
}

function Info() {
  return (
    <div>
      <Typography variant="body2" style={{ margin: "2pt" }}>
        made with{" "}
        <img
          style={{ height: "8pt" }}
          src="data:image/svg+xml;base64,"
          alt="React"
        />{" "}
        ReactJS
        <br />
        and{" "}
        <img
          style={{ height: "8pt" }}
          src="https://material-ui.com/static/logo_raw.svg"
          alt="material-ui"
        />{" "}
        Material-UI
      </Typography>
      <Divider light={true} style={{ background: "#ffffff55", margin: "4pt auto 2pt auto" }} />
      <Typography variant="body2">© 2020 Zhiyuan Wu</Typography>
    </div>
  );
}

function ContactBar(props) {
  const placement = { tooltip: props.isVertical ? "top" : "left" };
  const socials = [
    <GitHub {...placement} />,
    <LinkedIn {...placement} />,
    <Email {...placement} />,
  ];
  return !props.isVertical ? (
    <Drawer
      variant="permanent"
      anchor="right"
      open
      PaperProps={{ elevation: props.elevation }}
      style={{ padding: "8pt" }}
    >
      {socials}
      <div style={{ margin: "auto" }} />
      <Divider />
      <Tooltip title={<Info />} placement="left-end" arrow>
        <InfoOutlinedIcon
          style={{
            margin: "8pt auto 8pt auto",
            color: "grey",
          }}
        />
      </Tooltip>
    </Drawer>
  ) : (
    <Box
      component={Paper}
      display="flex"
      alignItems="center"
      flexWrap="wrap"
      square
      style={{
        width: "100%",
      }}
      elevation={props.elevation}
    >
      <Box display="flex" alignItems="center" flexWrap="nowrap">
        {socials}
      </Box>
      <Box
        display="flex"
        flexWrap="nowrap"
        style={{
          flex: "1 1 auto",
        }}
        flexDirection="row-reverse"
      >
        <Tooltip title={<Info />} placement="top-end" arrow>
          <InfoOutlinedIcon style={{ margin: "8pt", color: "grey" }} />
        </Tooltip>
      </Box>
    </Box>
  );
}

export default ContactBar;
